html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 20px;
  background-color: #cecece;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
}
@media only screen and (min-width: 1000px) {
  body {
    padding: 1.5rem;
  }
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 1.5rem;
  }
}

.task_container {
  background-color: #ffffff;
}
.task_container .items_container {
  padding: 5px;
}
.task_container .items_container .item {
  border: 1px solid #cecece;
  max-width: 150px;
  width: 100%;
}
.task_container .items_container .item_img {
  display: block;
  width: 100%;
  height: initial;
  object-fit: contain;
  object-position: center;
}
.task_container .items_container .item_title, .task_container .items_container .item_price {
  display: block;
  text-align: center;
}
.task_container .items_container .item_price {
  font-weight: 700;
}
.task_container .items_container#task_1 {
  width: 300px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 5px;
}
.task_container .items_container#task_1 .item_img {
  height: 70px;
}

.logo_container {
  padding: 5px;
}
.logo_container img {
  display: block;
  max-width: 100%;
  height: initial;
  object-fit: contain;
}